import React from "react"
import { previewLink } from "../link-resolver"
import { graphql } from "gatsby"
import { ContentPageComponent } from "../components/pages/content-page.coomponent"

const NotFoundPage = (/*{ data }*/ ...args) => {
  console.log(args)
  /*const page = data.prismic.allContentPages.edges[0]
  if (page) {
    return <ContentPageComponent page={page.node} />
  }*/
  return (
    <>
      <h1>NOT FOUND</h1>
      <p>You just hit a route that doesn&#39;t exist... the sadness.</p>
    </>
  )
}

NotFoundPage.fragments = [previewLink]

export default NotFoundPage

export const query = graphql`
  query notFoundPageQuery {
    prismic {
      allContentPages(firstPublicationDate: null) {
        edges {
          node {
            _meta {
              uid
              type
              lang
            }
            bgImage
            bgColor
            text
            title
            pageTitle
            pageDescription
            pageKeywords {
              keyword
            }
            pagePreviewImage
            asideNavigation {
              ... on PRISMIC_AsideNavigation {
                text
                _linkType
                body {
                  ... on PRISMIC_AsideNavigationBodyMenu {
                    type
                    label
                    fields {
                      link {
                        ...previewLink
                      }
                      icon
                      linkStyle
                      linkText
                    }
                    primary {
                      navLink {
                        ...previewLink
                      }
                      navText
                    }
                  }
                }
              }
            }
            body {
              ... on PRISMIC_ContentPageBodyPricingPlans {
                type
                label
                fields {
                  priceUnits
                  planPrice
                  planName
                  planDetails
                  linkText
                  linkStyle
                  link {
                    ...previewLink
                  }
                  isFreePlan
                }
                primary {
                  bgImage
                  bgColor
                  title
                  text
                }
              }
              ... on PRISMIC_ContentPageBodyItemsCollection {
                label
                type
                primary {
                  bgColor
                  bgImage
                  text
                  title
                  linkStyle
                  linkText
                }
                fields {
                  tag
                }
              }
              ... on PRISMIC_ContentPageBodyText {
                type
                label
                primary {
                  title
                  text
                  bgColor
                  bgImage
                }
              }
              ... on PRISMIC_ContentPageBodyFeature {
                type
                label
                primary {
                  bgColor
                  bgImage
                  text
                  title
                }
                fields {
                  image
                  linkStyle
                  linkText
                  text
                  title
                  link {
                    ...previewLink
                  }
                }
              }
              ... on PRISMIC_ContentPageBodyBlockWithTextAndImage {
                label
                type
                primary {
                  bgColor
                  bgImage
                  minHeight
                  title
                  text
                  image
                  link {
                    ...previewLink
                  }
                  linkStyle
                  linkText
                }
              }
              ... on PRISMIC_ContentPageBodyForm {
                type
                label
                primary {
                  bgColor
                  bgImage
                  formScript
                  formUrl
                  text
                }
              }
            }
          }
        }
      }
    }
  }
`
